import { getPostBySlug } from '@/services/blogService';
import { notFound } from 'next/navigation';
import { format } from 'date-fns';
import { CATEGORY_LABELS } from '@/types/blog';
import { useLocale } from 'next-intl';
import Image from 'next/image';

interface BlogPostPageProps {
    params: Promise<{
        slug: string;
        locale: string;
    }>;
}

export default async function BlogPostPage({ params }: BlogPostPageProps) {
    const { slug, locale } = await params;
    // Note: In Next.js 13+ app directory with Firebase Client SDK, basic reads often work in Server Components.
    // If this fails, we might need 'use client' or firebase-admin.
    const post = await getPostBySlug(slug);

    if (!post) {
        return notFound();
    }

    // Simple category label lookup helper (since we are on server, can't use useLocale hook directly if not client? actually we pass locale via params)
    const categoryLabel = CATEGORY_LABELS[post.category][locale as 'en' | 'ko'] || post.category;

    return (
        <article className="container mx-auto px-4 py-8 max-w-3xl">
            <div className="mb-8 text-center">
                <div className="flex justify-center gap-2 mb-4">
                    <span className="text-sm font-semibold text-primary px-3 py-1 bg-primary/10 rounded-full">
                        {categoryLabel}
                    </span>
                </div>
                <h1 className="text-3xl md:text-4xl font-bold mb-4 leading-tight">{post.title}</h1>
                <div className="text-muted-foreground text-sm flex justify-center items-center gap-4">
                    {post.authorPhotoUrl && (
                        // eslint-disable-next-line @next/next/no-img-element
                        <img src={post.authorPhotoUrl} alt={post.authorName} className="w-6 h-6 rounded-full" />
                    )}
                    <span>{post.authorName}</span>
                    <span>•</span>
                    <span>
                        {post.createdAt?.seconds
                            ? format(new Date(post.createdAt.seconds * 1000), 'yyyy.MM.dd')
                            : format(new Date(), 'yyyy.MM.dd')}
                    </span>
                </div>
            </div>

            {post.thumbnailUrl && (
                <div className="relative w-full aspect-video mb-12 rounded-xl overflow-hidden shadow-lg">
                    <Image
                        src={post.thumbnailUrl}
                        alt={post.title}
                        fill
                        className="object-cover"
                        priority
                        sizes="(max-width: 768px) 100vw, (max-width: 1200px) 75vw, 60vw"
                    />
                </div>
            )}

            <div
                className="prose prose-lg dark:prose-invert max-w-none"
                dangerouslySetInnerHTML={{ __html: post.content }}
            />
        </article>
    );
}
